.Key {
	display: -ms-flexbox;
	display: flex;
	height: 150px;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	border: 1px solid rgba( 0, 0, 255, 0.2 );
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border-radius: 10px;
	-webkit-transition: background .4s ease-in !important;
	-o-transition: background .4s ease-in !important;
	transition: background .4s ease-in !important;
	position: relative;
	text-align: center;
}
.Key--triggered {
	background: rgba( 0, 0, 255, 0.2 );
}



